.list__item {
	border: 1px solid teal;
	padding: 10px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.list__word {
	font-size: 30px;
	line-height: 38px;
}
.list__btn {
	font-size: 30px;
	color: #fff;
	border-width: 0;
	background-color: rgb(0, 89, 255);
	cursor: pointer;
}
