.generator {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 0px 0px 30px 0px;
	flex-wrap: wrap;
	row-gap: 20px;
}

.generator__word {
	font-size: 30px;
	line-height: 38px;
}

.generator__generate-btn {
	background-color: rgb(0, 89, 255);
	color: #fff;
	font-size: 24px;
	padding: 10px 30px;
	border-width: 0;
	cursor: pointer;
}

.generator__add-btn {
	background-color: rgb(0, 89, 255);
	color: #fff;
	font-size: 24px;
	padding: 10px 30px;
	border-width: 0;
	flex: 1 1 auto;
	cursor: pointer;
}